App{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

section{
    height: 100vh;
    width: 100%;
    background-color: #F28AB2;
    display: flex;
    justify-content: center;
    align-items: center;
}

main{
    height: auto;
    width: 300px;
    background-color: #FFFFFF;
    text-align: center;
    border-radius: 30px;
}

button{
    background-color: black;
    color: white;
    height: 30px;
    border-radius: 20px;
    width: 100px;

}

rt{
    font-size: 15px;
}

ruby p{
    font-size: 10px;
    color: gray;
    text-align: center;
}

img{
    border-radius: 50%;
}



